<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <div class="mall_list">
      <div class="mall_item" v-for="(item,index) in list" @click="toDetailPage(index)">
        <img :src="item.imageURL" alt="">
        <div class="mall_item_name">{{item.name}}</div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "index",
    data(){
      return {
        userId: '',
        title: '旗舰店',
        list:[
          {imageURL:'../../../static/images/mall/list_item1.png',name:'中诚信国际信用评级有限公司'},
          {imageURL:'../../../static/images/mall/list_item2.png',name:'中债资信评估有限责任公司'},
          {imageURL:'../../../static/images/mall/list_item3.png',name:'国泰君安证劵'},
        ]
      }
    },
    methods:{
      onClickLeft(){

      },
      toDetailPage(index){
        if(index === 2){
          if(this.userId){
            this.$router.push('/mall/gtja');
          }else{
            this.$dialog.confirm({
              title: '友情提示',
              message: '您还未登录或登录已超时，请先登录？'
            }).then(() => {

            }).catch(() => {

            });
          }
        }else{
          this.$router.push('/mall/detail/' + index);
        }
      }
    },
    created() {
      this.userId = this.$route.query.userId;
    }
  }
</script>

<style scoped>

  .mall_list {
    position: absolute;
    top: 46px;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 15px;
    background: rgba(247, 248, 250, 1);
  }
  .mall_item {
    width: 100%;
    height: 75px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.19);
    border-radius: 2px;
    margin-bottom: 10px;
    position: relative;
  }
  .mall_item img {
    width: 60px;
    height: 60px;
    position: absolute;
    left: 11px;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  .mall_item_name {
    position: absolute;
    top: 25.5px;
    left: 85.5px;
    right: 11px;
    font-size: 16px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 22.5px;
    letter-spacing: 0.5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>



